<template>
  <div class="analyzeObject">
    <div class="tableTopTools clearfix">
      <p class="toolName fl">工具栏：</p>
      <div class="buttons fl">
        <a-button type="primary" @click="optObject">
          <a-icon type="plus"></a-icon>添加分析对象
        </a-button>
      </div>
      <div class="searchInput fr">
        <a-input-search v-model="searchInput" placeholder="请输入分析对象名称" @change="keywordChange"></a-input-search>
      </div>
    </div>
    <div class="tableContent">
      <a-table :columns="columns" :dataSource="objectData" rowKey="id" :pagination="false">
        <span slot="gmtCreate" slot-scope="text,record">
          <span>{{record.gmtCreate | dateFormatter(1)}}</span>
        </span>
        <span slot="gmtModified" slot-scope="text,record">
          <span>{{record.gmtModified | dateFormatter(1)}}</span>
        </span>
        <span slot="action" slot-scope="text,record">
          <a class="t-optColor-normal" @click="editObject(record)">编辑</a>
          <a class="t-optColor-warning" @click="deleteObject(record)">删除</a>
        </span>
      </a-table>
      <div class="ar" style="margin-top:30px" v-if="objectData.length > 0">
        <a-pagination
          v-model="pageParam.page"
          :total="total"
          :page-size="pageParam.size"
          @change="pageChange"
        ></a-pagination>
      </div>
    </div>
    <objectModal
      :objectVisible="objectVisible"
      @objectOk="objectOk"
      @objectCancel="objectCancel"
      :content="content"
      :optType="optType"
    ></objectModal>
  </div>
</template>
<script>
import objectModal from "@/components/myModal/object-modal.vue";
export default {
    name:'analyzeObject',
    components:{objectModal},
    data() {
    return {
      // 表格列
      columns: [
        {
          dataIndex: "objectName",
          title: "分析对象名称",
        },
        {
          dataIndex: "code",
          title: "分析对象唯一标识",
        },
        {
          dataIndex: "objectType",
          title: "分析对象类型",
        },
        {
          dataIndex: "description",
          title: "描述",
        },
        {
          dataIndex: "creator",
          title: "创建者",
        },
        {
          dataIndex: "gmtCreate",
          title: "创建时间",
          scopedSlots: { customRender: "gmtCreate" },
        },
        {
          dataIndex: "gmtModified",
          title: "最近更新时间",
          scopedSlots: { customRender: "gmtModified" },
        },
        {
          dataIndex: "action",
          title: "操作",
          scopedSlots: { customRender: "action" },
        },
      ],
      // 表格数据
      objectData: [],
      // 操作模态框的显示 隐藏
      objectVisible: false,
      // 操作模态框的内容
      content: {
        objectName: "",
        objectType: "",
        code:"",
        description: "",
        creator: "",
        gmtCreate: "",
        gmtModified: "",
      },
      // 模态框的操作类型 0添加 1编辑
      optType: 0,
      pageParam: {
        page: 1,
        size: 10,
      },
      searchInput: "",
      total: 0,
    };
  },
  created() {
    this.getObject();
  },
  methods: {
    //  查询分析对象
    getObject() {
      let params = Object.assign({}, this.pageParam, {
        objectName: this.searchInput,
      });
      this.$API.searchObject(params).then((res) => {
        if (res.code === 1000) {
          this.objectData = res.data;
          this.total = res.total;
          if (this.pageParam.page > 1 && this.objectData.length === 0) {
            this.pageParam.size--;
            this.getObject();
          }
        } else {
          this.objectData = [];
        }
      });
    },
    optObject() {
      this.resetContent()
      this.objectVisible = true;
      this.optType = 0;
    },
    //   编辑
    editObject(column) {
      this.content = column;
      this.optType = 1;
      this.objectVisible = true;
    },
    // 删除
    deleteObject(column) {
      const that = this;
      this.$confirm({
        title: "确定删除该条数据吗？",
        content: () => <div style="color:red">删除后将不可恢复</div>,
        okText: "确认删除",
        cancelText: "取消删除",
        onOk() {
          that.$API.delObject(column.id).then((res) => {
            if (res.code === 1000) {
              that.$message.success("删除成功");
              that.getObject();
            } else {
              that.$message.warning(res.message);
            }
          });
        },
      });
    },
    // 添加unit
    objectOk(val) {
      if (val.optType) {
        let param = Object.assign({}, val.formContent, { id: this.content.id });
        this.$API.editObject(param).then((res) => {
          if (res.code === 1000) {
            this.$message.success("分析对象更新成功");
            this.objectVisible = false;
            this.getObject();
          } else {
            this.$message.warning(res.message);
          }
        });
      } else {
        this.$API.addObject(val.formContent).then((res) => {
          if (res.code === 1000) {
            this.$message.success("添加分析对象成功");
            this.objectVisible = false;
            this.getObject();
          } else {
            this.$message.warning(res.message);
          }
        });
      }
    },
    // 取消unit
    objectCancel() {
      this.objectVisible = false;
      this.resetContent();
    },
    pageChange() {
      this.getObject();
    },
    // 搜索关键词变化事件
    keywordChange() {
      this.pageParam.page = 1;
      this.getObject();
    },
    // 清洗content
    resetContent() {
      this.content = {
        objectName: "",
        objectType: "",
        code: "",
        description: "",
        creator: "",
        gmtCreate: "",
        gmtModified: "",
      };
    },
  },
}
</script>