<template>
  <div class="object-modal">
    <a-modal
      :title="title"
      :visible="objectVisible"
      @cancel="handleCancel"
      cancelText="取消"
      okText="确定"
      :mask="false"
      :maskClosable="false"
      :width="600"
      class="modalCls"
    >
      <a-form-model ref="objectForm" :model="objectForm" :rules="objectFormRule">
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="分析对象名称" prop="objectName">
              <a-input v-model="objectForm.objectName" placeholder="请输入分析对象名称" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="分析对象唯一标识" prop="code">
              <a-input v-model="objectForm.code" placeholder="请输入分析对象唯一标识" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="分析对象类型" prop="objectType">
                <a-select v-model="objectForm.objectType" placeholder="请选择分析对象类型" allowClear>
                  <a-select-option value="email">邮箱日志</a-select-option>
                  <a-select-option value="web">web日志</a-select-option>
                </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="分析对象描述" prop="description">
              <a-textarea v-model="objectForm.description" placeholder="请输入分析对象描述" rows="4"></a-textarea>
            </a-form-model-item>
          </a-col>
        </a-row>
      </a-form-model>
      <template slot="footer">
          <a-button type="link" ghost @click="handleCancel">取消</a-button>
          <a-button type="primary" @click="handleOk">提交</a-button>
      </template>
    </a-modal>
  </div>
</template>
<script>
export default {
  name: "object-modal",
  props: {
    // 模态框的显示与隐藏
    objectVisible: {
      type: Boolean,
      default: false,
      required: true,
    },
    // 模态框的标题
    title: {
      type: String,
      default: "添加/编辑分析对象",
    },
    // 表单内容 一般添加的时候才会传入
    content: {
      type: Object,
      default: function () {
        return {
          objectName: "",
          objectType: "",
          description: "",
          creator: "",
          gmtCreate: "",
          gmtModified: "",
        };
      },
    },
    // 关于操作类型 0为添加 1为编辑
    optType:{
      type:Number,
      default:0,
      required: true,
    }
  },
  data() {
    return {
      objectFormRule: {
        objectName: [{ required: true, message: "请输入分析对象名称",trigger:'blur' }],
        objectType: [{ required: true, message: "请输入分析对象类型",trigger:'change' }],
        code: [{ required: true, message: "请输入分析对象唯一标识",trigger:'blur' }],
      },
      objectForm:{
          objectName: "",
          objectType: "",
          code: "",
          description: "",
      }
    };
  },
  methods: {
    handleOk() {
        this.$refs.objectForm.validate(valid => {
            if(valid){
                console.log(this.objectForm)
                this.$emit("objectOk", {formContent:this.objectForm,optType:this.optType});
            } else {
                this.$message.warning('输入有误，请检查');
                return false
            }
        })
    },
    handleCancel() {
      this.$emit("objectCancel", "我点击取消");
    },
  },
  watch:{
    content:{
      handler:function(newVal){
        Object.assign(this.objectForm,newVal);
      },
      deep:true
    }
  }
};
</script>
